@import url("https://fonts.googleapis.com/css2?family=Roboto:wght@300;700&display=swap");
*,
*::before,
*::after {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

html {
  font-size: 62.5%;
  scroll-behavior: smooth;
}

body {
  font-family: "Roboto", sans-serif;
  font-size: 1.6rem;
}

.module {
  display: flex;
  flex-direction: column;
  max-width: 600px;
  height: 100vh;
  margin: auto;
  text-align: center;
  padding: 1rem;
}
.module__image {
  height: 30%;
  width: 100%;
}
.module__image img {
  object-fit: cover;
  height: 100%;
}
.module__container {
  display: flex;
  flex-direction: column;
  flex-grow: 1;
  margin-top: 2rem;
}
.module__text {
  margin-top: 2rem;
  line-height: 2.4rem;
  color: hsl(0deg, 0%, 7%);
}
.module__title {
  font-size: 3rem;
  color: hsl(0deg, 0%, 7%);
}
.module__box {
  margin-top: 3rem;
}
.module__button {
  background-color: hsl(21deg, 50%, 79%);
  color: hsl(0deg, 0%, 100%);
  font-weight: 700;
  padding: 1rem 3rem;
  margin-bottom: 2rem;
  border-radius: 3.2rem;
  border: none;
  cursor: pointer;
}
.module__button:hover {
  background-color: rgb(240, 213, 200);
}
.module__button--modal {
  margin-top: 2rem;
}
.module__counter {
  font-weight: 700;
}
.module__modal {
  display: none;
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background-color: hsla(0deg, 0%, 7%, 0.5);
}
.module__modal__counter {
  font-weight: 700;
}
.module__modal-content {
  display: flex;
  position: relative;
  z-index: 1;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;
  width: 100%;
  padding: 4rem;
  background-color: hsl(0deg, 0%, 100%);
}
.module__modal-icon {
  position: absolute;
  right: 2rem;
  top: 2rem;
  cursor: pointer;
}

@media (min-width: 992px) {
  .module {
    max-width: 1440px;
    flex-direction: row;
    justify-content: center;
    text-align: left;
    height: 36vh;
    max-height: 300px;
    min-height: 300px;
    margin: 9.6rem auto;
    padding: 0;
  }
  .module__container {
    margin: 0;
    flex-basis: 33%;
    flex-grow: 0;
    margin-left: 4rem;
    justify-content: center;
  }
  .module__box {
    flex-grow: 0;
  }
  .module__image {
    flex-basis: 33%;
    font-size: 0;
    height: 100%;
    margin-right: 4rem;
  }
  .module__modal-content {
    align-items: flex-start;
    justify-content: space-around;
    width: 70%;
    max-width: 1000px;
    padding: 2rem 9.5rem;
    background-color: hsl(0deg, 0%, 100%);
  }
  .module__modal-icon {
    position: absolute;
    right: 0;
    top: 0;
    padding: 2.5rem;
    cursor: pointer;
  }
}
.active {
  display: flex !important;
}